.SubscribedList {
  background-color: #D6D3D1;
  padding: 0.5rem 0; }
  .SubscribedList .horizontalList {
    height: 100%; }
  .SubscribedList ul {
    height: 100%;
    overflow: auto;
    white-space: nowrap; }
  .SubscribedList li {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: #74706D;
    margin-left: 1rem;
    display: inline-block; }
  .SubscribedList li:last-child {
    margin-right: 1rem; }



